const toUserSite = (user) => {
    let site = ""
    switch (user) {
      case "": {
        site = "/transport";
        break;
      }
      case "admin": {
        site = "/admin";
        break;
      }
      default: {
        site = "/wpuphones";
        break;
      }
    }
    return site
  }
export default toUserSite