import React from "react";
import formStyle from "./formStyle.module.css";
import Logo from "../../styles/svg/wpu/wpuLogoNoTitle.svg";
import CopyRightFooter from "../CopyRightFooter/CopyRightFooter.jsx";
import toUserSite from "../auxiliary/toUserSite";
import GeneralHeader from "../GeneralHeader/GeneralHeader";
import { useFormik } from "formik";
import { useNavigate } from "react-router-dom";
import { useUser } from "../../contexts/userContext.js";


const SignupForm = () => {
  const { user, setUser } = useUser();
  const navigate = useNavigate();


  const validate = (values) => {
    const errors = {};
    if (!values.fullName) {
      errors.fullName = "  ...";
    } else if (values.fullName.length < 4) {
      errors.fullName = "   4   ";
    } else setUser(values.fullName);

    if (!values.password) {
      errors.password = "  ...";
    } else if (values.password.length < 10) {
      errors.password = "   10   ";
    }
    return errors;
  };

  const formik = useFormik({
    initialValues: {
      fullName: "",
      password: "",
      user:""
    },
    validate,
    // onChange: (values)=>{
    //   alert(JSON.stringify(values, null, 2));
    // },
    onSubmit: (values) => {
      setUser(values.fullName);
      localStorage.setItem("user",user)
    },
  });

  const onSubmit = (e) => {
      e.preventDefault();
      formik.handleSubmit();
      navigate(toUserSite(user));
  }

  return (
    <>
      <div className="appContainer">
        <header className="appHeader">
          <GeneralHeader/>
        </header>
        <main className="appBody">
          <div className={formStyle.wpuformcontainer}>
            <div className={formStyle.wpuformlogocontainer}>
              <img src={Logo} alt="LOGO" className={formStyle.wpulogo} />
            </div>
            <h1 className={formStyle.formtitle}>  </h1>
            <form className={formStyle.wpuform} onSubmit={onSubmit}>
              <div className={formStyle.wpusubform}>
                <label htmlFor="fullName" className={formStyle.wpulabel}>
                   
                </label>
                <input
                  id="fullName"
                  name="fullName"
                  placeholder=" "
                  type="text"
                  onChange={formik.handleChange}
                  value={formik.values.fullName}
                  className={formStyle.wpuinput}
                />
                {formik.errors.fullName ? (
                  <div className={formStyle.wpup}>{formik.errors.fullName}</div>
                ) : null}
                </div>
              <div className={formStyle.wpusubform}>
                <label htmlFor="password" className={formStyle.wpulabel}>
                   
                </label>
                <input
                  id="password"
                  name="password"
                  type="password"
                  autoComplete="true"
                  onChange={formik.handleChange}
                  value={formik.values.password}
                  className={formStyle.wpuinput}
                />
                {formik.errors.password ? (
                  <div className={formStyle.wpup}>{formik.errors.password}</div>
                ) : null}
              </div>
              <div
                style={{
                  display: "flex",
                  gap: ".5rem",
                  justifyContent: "flex-end",
                }}
              >
                <button type="submit" className={formStyle.wpubutton}>
                  
                </button>
              </div>
            </form>
          </div>
        </main>
        <footer className="appFooter"> <CopyRightFooter /> </footer>
      </div>
    </>
  );
};

export default SignupForm;
